 //te sirve para darle Smooth scroll a los enlaces a las diferentes secciones
 function scrollNav() {
     //selecciona los enlaces:
     const enlaces = document.querySelectorAll(".navegacion-principal a");
     //indenta cada enlace, extrae el valor del href, y genera el smooth scroll:
     enlaces.forEach(function(enlace) {
         enlace.addEventListener("click", function(e) {
             e.preventDefault();
             const seccion = document.querySelector(e.target.attributes.href.value);


             seccion.scrollIntoView({
                 behavior: "smooth",

             });
         });
     });
 };


 // When the user scrolls the page, execute myFunction
 window.onscroll = function() { navFijo(), scrollFunction() };

 // Get the header
 var header = document.getElementById("header");

 // Get the offset position of the navbar
 var sticky = header.offsetTop;

 // Add the sticky class to the header when you reach its scroll position. Remove "sticky" when you leave the scroll position
 function navFijo() {
     if (window.pageYOffset > sticky) {
         header.classList.add("fijo");
     } else {
         header.classList.remove("fijo");
     }
 }

 //boton hacia arriba:
 //Get the button:
 botonTop = document.getElementById("myBtn");

 // When the user scrolls down 20px from the top of the document, show the button


 function scrollFunction() {
     if (document.body.scrollTop > 88 || document.documentElement.scrollTop > 88) {
         botonTop.style.display = "block";
     } else {
         botonTop.style.display = "none";
     }

 }

 // When the user clicks on the button, scroll to the top of the document
 function topFunction() {
     window.scrollTo({ top: 0, behavior: 'smooth' });

     // For Chrome, Firefox, IE and Opera
 }